import React, { useState } from 'react';
import './about.css';
// import Modal from "./modal";
import Video from "../assets/3d_video.mp4"
import { Link } from "gatsby";


let image_index = 0;
const garelly_sources = [
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
  "img/project1.jpg",
  "img/project2.jpg",
  "img/project3.jpg",
];


const Project_image_garelly = () => {

  const [modal_class, setModal_class] = useState("modal_hidden");
  const [pick_image, setPick_image] = useState("");
  
  const img_clicked = (clicked_index) => {
    console.log("index " + clicked_index);
    image_index = clicked_index;
    console.log(image_index);
    setModal_class("");
    setPick_image(garelly_sources[image_index]);
  }

  const handleClose = () => {
    setModal_class("modal_hidden")
  }

  const handleArrow = (arrowNum) => {
    if(arrowNum === 1) {
      if(image_index == garelly_sources.length - 1) {
        image_index = 0;
        console.log(image_index);
        setPick_image(garelly_sources[image_index])
      }
      else {
        image_index++;
        console.log(image_index);
        setPick_image(garelly_sources[image_index])
      }
    }
    else if(arrowNum === 0){
      if(image_index == 0) {
        image_index = garelly_sources.length - 1;
        console.log(image_index);
        setPick_image(garelly_sources[image_index]);
      }
      else {
        image_index--;
        console.log(image_index);
        setPick_image(garelly_sources[image_index]);
      }
    }
  }



  return(
    <>
      <div className={modal_class +" modal_container"}>
        <div>
          <button 
            className="icon_modal_button"
            onClick={() => handleArrow(0)}
          >
            <i className="fas fa-chevron-left fontFour"></i>
          </button>
        </div>
        <div className="white_box">
          <button 
            className="close_modal_button fontFour"
            onClick={handleClose}
          >&times;</button>
          <span>
            <Link to="/">
              <button className="link_category">Learn More</button>
            </Link>
            <h2 className="modal_title fontTwo">Project Title</h2>
            <img 
              src={pick_image}
              className= "modal_image"
            ></img>
            <h3 className="fontTwo">I am the Goran san ! Bla bla bla bla bla~</h3>
          </span>
        </div>
        <div>
          {/* <button 
            className="icon_modal_button"
            onClick={() => handleArrow(1)}
          >
            <i className="fas fa-chevron-right fontFour"></i>
          </button> */}
        </div>
      </div>

      <div className="garelly_container">
        <div className="inner_container">
          <h1>Our Projects</h1>
            <div className="border"></div>
            <div className="garelly">
              {garelly_sources.map((e, i) => 
                <div 
                  className="img_box"
                  key={i}
                  onClick={() => img_clicked(i)}
                >
                  <img 
                  src={e} 
                  alt="Project images"
                  />
                </div>
              )}
            </div>
            <div className="additional_space"></div>
            <button 
              className="show_more fontTwo">
                Show More  <i className="fas fa-caret-down fontTwo"></i>
            </button>
        </div>
      </div>
    </>
  );
};


const About = () => {
  
  return (
    <div id="about">
      <Project_image_garelly />
      
      <div className="aboutContainer">
        <div className="flex_right flexHeight">
            <video autoPlay loop preload="auto">
              <source src={Video} type="video/mp4"></source>
              <track default/>
            </video>
        </div>
        <div className="flex_left flexHeight">
          <div className="fontFour textContainer">
            <h2>Our Services</h2>
            <p>Our mission is your wish</p>
            <Link to="/service">
              <i className="fas fa-angle-double-left">
              </i>  Learn more 
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default About;